@import "main";

a:link {
  text-decoration: none;
}

a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

a:active {
  text-decoration: none;
}

//for default layout-body div(includes all)
.body {
    max-width: 90%;
    min-width:70%;
    margin: auto;
}

//for include-header div
.header {
    width:100%;
    margin-bottom: 2em;
    font-family: 'Black Han Sans', sans-serif;
    color : black;
}

.logo {
    float: left;
    margin-left: 10px;
    margin-right: 30px;
    font-size: 2em;
}

.nav {
    position: relative;
    top:0.3em;
    font-size: 1.5em;

    ul {
        list-style: none;
        position: relative;
    }

    a {
      margin:10px;
      position: relative;
    }

    a:hover {
      color: rgb(104, 62, 0);
    }
}

//컨텐츠 들어가는 바디
.body {
  margin: 3%;
}

//과제 페이지 썸네일
.thumb-container {
  max-width: 90%;
  padding:30px;
}

.thumb-unit {
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
      display: inline-block;
    max-width: 43%;
    min-width: 200px;
    max-height: 90%;
    min-height: 100px;
    margin: 0px 10px 30px;
  max-width: 90%;
  min-width: 60%;
  max-height: 90%;
  min-height: 100px;
  margin: 0px auto 30px;
  position: relative;
  overflow: hidden;
  background-position: center; /* Center the image */
  background-size: cover;
  background-blend-mode: color-dodge;
  text-align: center;
}

.thumb-unit :hover {
  background-color : gray;
}

.thumb-overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
}


